

<template>
    <div class="CatVote">
        <transition  name="slide" type="animation">
            <div class="alert" v-if="show" >Vous avez votez pour <span>{{nameCate}}</span></div>
        </transition>
         <div><p>Clic sur le chat que tu trouves le plus mignons</p></div>
         <ul class="CatVote__wrap" >    
            <li :class="{ 'disabled': isdisabled }" @click.prevent="vote(cat)"  
                v-for="(cat,index) in twoCats"  
                :key="index"  
                class="CatVote__item">
                    <Cat  v-if="cat"
                    :key="index"
                    :title='cat.item.id' 
                    :like='cat.item.like'
                    :urlImage='cat.item.url' />  
            </li>
        </ul>
       
    </div>
</template>

<script>
import { mapGetters } from 'vuex';
import { mapActions } from 'vuex';
import Cat from '../components/Cat'
import * as firebase from 'firebase'
import config from '../../firebaseConfig';

    export default {
        components: {
            Cat,
        },
        data() {
            return {
                nameCate: '',
                show:false,
                isdisabled:false,
                refCat:[]
            }
        },
        created () {
            this.getCats()
        },
        computed: {
            ...mapGetters  ({
                cats: 'catsList',
                twoCats:'twoCats'
            }),
        },
        methods: {
            ...mapActions([
                'getCats',
                'getTwoCats',
                'updateCat'
            ]),
            vote(cat) {
                this.isdisabled = true; 
                this.updateCat(cat);
                this.nameCate = cat.item.id
                this.show = true;
                setTimeout( () => this.afterVote(),100);
            },
            afterVote() {
                this.getTwoCats();
                this.show = false;
                this.isdisabled = false;
            }
        },
      
    }
</script>

<style lang="scss" scoped>
@import "../assets/scss/_variables.scss";
    .CatVote {
       
        &__wrap {
            list-style-type: none;
            padding:0;
            margin:0;
            display: flex;
          
            flex-direction: column;
            justify-content: center;
        }
        &__item {
            cursor: pointer;
             padding: 20px;
            &:nth-child(even) {
                background-color:#7FAB89;
            }
            &:nth-child(odd) {
                background-color:#97CAA3;
            }
            &.disabled {
                pointer-events: none;
            }
        }
    }
    .alert {
        padding: 10px;
        font-size: 14px;
        color:#fff;
        background-color: $color-primary;
        position: absolute;
        top:80px;
        right:10%;
        width:200px;
        margin-left:-150px;
        span {
            font-weight:bold;
            font-size: 20px;
        }
    }

    //animations
    .slide-enter {

    }
     .slide-move {
        transition: transform 1s;
    }
    .slide-enter-active {
        animation: slide-in 1s ease-out forwards;
    }
    .slide-leave {
    }
    .slide-leave-active {
        animation: slide-out 1s ease-out forwards;
        position: absolute;
    }

    @keyframes slide-in {
        from {
            transform: translateY(20px)
        }
        to {
            transform: translateY(0)
        }
    }

    @keyframes slide-out {
        from {
            transform: translateY(0)
        }
        to {
            transform: translateY(20px)
        }
    }

    @media screen and (min-width: $bp-sm)  {
        .CatVote {
            &__wrap {
                flex-direction: row;
            }
            &__item {
            width:calc(50% - 10px);
            
            }
        }
        .alert { 
            top:20px;
        }
    }

    @media screen and (min-width: $bp-md) {
    
    }
</style>